/* Cast.tsx */

.cast {
  display: flex;
  margin-top: 2rem;
  overflow: auto hidden;
  margin-bottom: 2rem;
  border-radius: 5px;
  animation: appear 1s 1 ease-in-out;
}

@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* 가로 스크롤바 */
.cast::-webkit-scrollbar {
  margin-top: 20px;
  height: 15px;
  background-color: black;
  -webkit-box-shadow:
    5px 5px 5px 0px #000000,
    inset 4px 4px 15px 0px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow:
    5px 5px 5px 0px #000000,
    inset 4px 4px 15px 0px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
}
.cast::-webkit-scrollbar-thumb {
  background: linear-gradient(rgb(43, 4, 15), rgb(197, 103, 161));
  border-radius: 150px;
  width: -20px;
  background-clip: padding-box;
}
.cast::-webkit-scrollbar-track {
  background-color: #310416;
}

/* 배우 이미지 */
.cast_img {
  width: 250px;
  height: 300px;
  border-radius: 5px;
  -webkit-box-shadow:
    0px 10px 13px -7px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow:
    0px 10px 13px -7px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  border: 1.1px solid black;
  border-bottom: 1px solid black;
}

/* 배우정보 */
.cast_info {
  padding-bottom: 10px;
  padding-top: 2px;
  border-radius: 5px;
  margin: 5px;
  border: 0.5px solid black;
  background: transparent;
  -webkit-box-shadow:
    5px 5px 5px 0px #000000,
    inset 4px 4px 15px 0px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow:
    5px 5px 5px 0px #000000,
    inset 4px 4px 15px 0px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
}
.cast_name {
  margin-top: 1rem;
  color: #ffffff;
}
.cast_info:hover {
  padding-top: 5px;
  z-index: 20;
  color: #ffffff;
  text-shadow:
    0 -1px 4px #fff,
    0 -2px 10px #ff0,
    0 -10px 20px #ff8000,
    0 -18px 40px #f00;
  transition: transform 1s ease-in-out;
  transform: scale(1.06);
}
